
.home-main.smui .page-header,.home-main.smui .page-header *{text-align:center !important
}
#components-list{width:700px;margin:0 auto;display:block
}
.comp-list:hover #components-list{width:100%;position:fixed;transition:width 3s ease-in-out
}
.home-main .logo-item:hover{transform:rotatey(360deg);transition-duration:2s;transition-timing-function:ease-in-out
}
.home-main .logo{display:inline-block;width:250px;height:250px;margin:30px 30px;border-radius:50%;background-image:url(http://cn.vuejs.org/images/logo.png);background-size:170px 170px;background-position:40px 56px;background-repeat:no-repeat;vertical-align:top;box-shadow:0 0 50px 50px rgba(3,169,244,0.26) inset
}
.home-main iframe{min-height:90vh
}
.home-main .page-footer{background-color:#212121;color:rgba(255,255,255,0.541176);font-size:16px
}
.home-main .page-footer .wrapper{width:800px;display:flex;margin:20px auto
}
.home-main .page-footer a{display:inline-block;line-height:30px;min-width:200px;color:rgba(255,255,255,0.7)
}
.home-main .vue-logo{background-image:url(http://cn.vuejs.org/images/logo.png);background-size:120px 120px;background-position:58px 77px
}
.home-main .webpack-logo{background-image:url(http://image.uc.cn/s/uae/g/2p/material/images/webpack.png);background-position:37px 40px
}
.home-main .doc-logo{background-image:url('http://image.uc.cn/s/uae/g/2p/material/images/doc.png');background-size:120px 120px;background-position:72px 67px
}
.home-main .flex{display:flex;justify-content:center
}
.home-main .flex-box{display:inline-block;color:#FFF;font-size:14px;min-width:300px;height:60vh;border-radius:10px;margin:20px 40px;padding:10px;position:relative;background-color:rgba(255,255,255,0.5)
}
.home-main .flex-box .info{width:500px
}
.home-main .flex-box.doc{color:#333;height:80vh;width:80vw;margin:10vh 10vw;background-color:rgba(255,255,255,0.6);overflow-y:scroll
}
.home-main .date-picker #canlendar-button{padding:10px 5px;background-color:#FFF
}
.home-main .fp-section{max-height:100vh;overflow-y:hidden;position:relative
}
.home-main .logo-item:hover .desc{filter:none
}
.home-main .desc{color:#FFF;font-size:14px;text-align:center;line-height:2em;border-radius:50%;filter:blur(5px);margin:0 20px;background-color:rgba(3,169,244,0.26)
}
.home-main .desc a{color:#FFF
}
.home-main .blur{content:'';position:absolute;height:100%;width:100%;top:0;left:0;z-index:-10;background:rgba(76,175,80,0.32)
}
.home-main .sky-1::before{content:'';position:absolute;height:100%;width:100%;top:0;left:0;z-index:-10;background:rgba(76,175,80,0.32);background-image:url('http://image.uc.cn/s/uae/g/2p/material/images/sky/sky-11.jpg')
}
.home-main .sky-2::before{content:'';position:absolute;height:100%;width:100%;top:0;left:0;z-index:-10;background:rgba(76,175,80,0.32);background-image:url('http://image.uc.cn/s/uae/g/2p/material/images/sky/sky-12.jpg')
}
.home-main .sky-3::before{content:'';position:absolute;height:100%;width:100%;top:0;left:0;z-index:-10;background:rgba(76,175,80,0.32);background-image:url('http://image.uc.cn/s/uae/g/2p/material/images/sky/sky-13.jpg')
}
.home-main .sky-4{content:'';position:absolute;height:100%;width:100%;top:0;left:0;z-index:-10;background:rgba(76,175,80,0.32);background-image:url('http://image.uc.cn/s/uae/g/2p/material/images/sky/sky-3.jpg')
}
.home-main .sky-5{content:'';position:absolute;height:100%;width:100%;top:0;left:0;z-index:-10;background:rgba(76,175,80,0.32);background-image:url('http://image.uc.cn/s/uae/g/2p/material/images/sky/sky-15.jpg')
}
.home-main .sky-6{content:'';position:absolute;height:100%;width:100%;top:0;left:0;z-index:-10;background:rgba(76,175,80,0.32);background-image:url('http://image.uc.cn/s/uae/g/2p/material/images/sky/sky-16.jpg')
}
.home-main .dialog{display:block;position:relative;margin:50px 100px
}
.home-main .layer.hidden{display:block
}
.home-main .rotage-box{min-width:100px;min-height:100px;padding:25px;position:absolute;right:100px;bottom:50px;z-index:9999
}
.fp-controller .fp-controller-dotted.fp-controller-dotted{width:10px;height:10px;margin-bottom:10px;border-radius:50%;background:rgba(255,255,255,0.13);border:1px solid rgba(0,188,212,0.77);transition:all .5s ease-in-out;cursor:pointer
}
.fp-controller-dotted:hover{box-shadow:0 0 5px 5px rgba(255,255,255,0.13)
}
.fp-controller .fp-controller-dotted.fp-controller-dotted.active{background:#159168;border-color:#fff
}


