
.mark-preview h1,.mark-preview h2,.mark-preview h3,.mark-preview h4{font-weight:bold;font-size:1.5em;line-height:1.5em;padding:10px;margin:10px 0
}
.mark-preview h1{font-size:3em;border-left:3px solid lightblue;background-color:#EEE
}
.mark-preview h2{font-size:2em;border-left:3px solid lightblue;background-color:#EEE
}
.mark-preview pre{padding:10px;background:rgba(255,255,255,0.7) !important;margin:10px 0;border:1px solid #EEE;border-left:5px solid #EEE
}
.mark-preview blockquote{border:1px solid #EEE;border-radius:2px;padding:10px
}
.mark-preview ul{margin-left:2em
}
.mark-preview li{list-style-type:square
}
.mark-preview pre code{background-color:#FFF;border:none;margin:0 2px
}


